import React, { useState } from "react";
import * as Yup from "yup";
import { makeStyles } from "@material-ui/core/styles";
import {
  Box,
  Card,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  TextField,
  Typography,
} from "@material-ui/core";
import toast from "react-hot-toast";
import axios from "axios";
import { Formik } from "formik";
import Save from "../Buttons/Save";
import ResetButton from "../Buttons/ResetButton";
import Design from "../Design";
import { Navigate, useLocation, useNavigate } from "react-router-dom";
import userService from "../apis/userService";
import {customAxios} from "../apis/Axios";
const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(1),
      width: "25ch",
      margin: "17px",
    },
  },
  typography: {
    padding: "17px",
  },
  head: {
    height: "200vh",
    paddingLeft: 30,
    paddingRight: 30,
    paddingTop: 10,
    background: "none",
  },
}));

// const Section = [
//   {
//     value: "Section A",
//     label: "0",
//   },
//   {
//     label: "1",
//     value: "Section B",
//   },
//   {
//     label: "2",
//     value: "Section C",
//   },
// ];

export default function AddClass() {
  const classes = useStyles();
  const { state } = useLocation();
  const Navigate = useNavigate();
  console.log(state);

  if (state == null) {
    var invoice = {
      className: "",
      description: "",
    };
  } else {
    state.invoice;
    var { invoice } = state;
  }
  return (
    <>
      <Card className={classes.head}>
        <Formik
          // enableReinitialize
          initialValues={{
            className: invoice.className || "",
            description: invoice.description || "",
          }}
          validationSchema={Yup.object().shape({
            className: Yup.string().max(255).required("Rollno is required"),
            // classteacher: Yup.string()
            //   .max(255)
            //   .required("Password is required"),
          })}
          onSubmit={async (values) => {
            // console.log("val", values);
            await new Promise((r) => setTimeout(r, 500));

            try {
              const Data = { ...values, id: invoice.id ? invoice.id : "" };
              customAxios
                .postCall("class/class", { ...Data })
                .then((res) => {
                  console.log("res", res);
                  if (res.status == 400) {
                    toast.success("Class Already Exist");
                  } else {
                    res.json;
                    // Navigate("../class");

                    toast.success(invoice.id ? "Class Updated" : "Class Added");
                  }
                });
              // alert(JSON.stringify(values, null, 2));
              // toast.success("Class Added");
              Navigate("../class");
            } catch (err) {
              console.log(err);
            }
          }}
        >
          {({
            errors,
            handleBlur,
            handleChange,
            handleSubmit,
            isSubmitting,
            dirty,
            touched,
            values,
            handleReset,
          }) => (
            <div>
              <Design text="Add Class" dashText="Add Class" />
              <Box style={{ paddingLeft: 20 }} boxShadow={20} color="black">
                <div className={classes.typography}>
                  <Typography variant="h5">
                    <strong>Add Class</strong>
                  </Typography>
                </div>
                <form noValidate onSubmit={handleSubmit}>
                  <div>
                    <TextField
                      autoComplete="off"
                      id="className"
                      label="Class Name"
                      style={{ margin: 8, width: "95%" }}
                      variant="outlined"
                      type="text"
                      error={Boolean(touched.className && errors.className)}
                      helperText={touched.className && errors.className}
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.className}
                    />

                    {/* <FormControl style={{ margin: 8, width: "95%" }}>
                      <InputLabel id="section">Class Dormitory</InputLabel>

                      <Select
                        fullWidth
                        labelId="section"
                        value={values.section}
                        onChange={handleChange}
                        id="section"
                        label="Department/Class"
                        name="section"
                      >
                        {Section.map((option) => (
                          <MenuItem key={option.value} value={option.value}>
                            {option.value}
                          </MenuItem>
                        ))}
                      </Select>
                    </FormControl> */}
                    <TextField
                      id="description"
                      label="description"
                      style={{ margin: 8, width: "95%" }}
                      fullWidth
                      error={Boolean(touched.description && errors.description)}
                      helperText={touched.description && errors.description}
                      onBlur={handleBlur}
                      onChange={handleChange}
                      margin="normal"
                      multiline
                      rows={4}
                      value={values.description}
                      variant="outlined"
                    />
                  </div>
                  <Save
                    text="Save"
                    variant="contained"
                    size="medium"
                    color="success"
                    type="submit"
                    disabled={isSubmitting}
                  />

                  <ResetButton
                    text="Reset"
                    variant="contained"
                    size="medium"
                    color="primary"
                    type="button"
                    onClick={handleReset}
                    disabled={!dirty || isSubmitting}
                  />
                </form>
              </Box>
            </div>
          )}
        </Formik>
        <Typography style={{ marginTop: 20 }}>
          © Copyrights <b>akkhor</b> 2019. All rights reserved. Designed by
          PsdBosS
        </Typography>
      </Card>
    </>
  );
}
